.ComponentMain {
  padding-top: 30px;
  padding-bottom: 30px;
  margin: auto;
  width: 90%;
  display: grid;
  grid-gap: 10px;
  -ms-flex-pack: center;
      justify-content: center;
  color:var(--color-5);
}
.displayGrid {
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
}
.displayTable {
  grid-template-columns: 1fr;
}



