.ComponentDisplay {
  height: 60px;
  width: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  z-index: 69;
  padding: 10px;
  position: absolute;
  right: 0;
  top: 0px;
}
.CoinsNumber {
  display: -ms-flexbox;
  display: flex;
}

#displayIcon{}
@media only screen and (max-width: 633px) {
    #displayIcon{
      display: none;
    }
    .ComponentDisplay{
      width: -webkit-fit-content;
      width: -moz-fit-content;
      width: fit-content;
      -ms-flex-pack: end;
          justify-content: flex-end;
      padding-right:20px
    }
    .CoinsNumberDescription{
      display: none;
    }
    .CoinsNumber{
      font-weight: 900;
    }
  }
  


