.sortDiv{
    position: fixed;
    bottom:10px;
    right:10px;
    display:-ms-flexbox;
    display:flex;
}
.sortButtonDiv{
    margin:0px 7px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    border-radius: 50%;
    z-index: 1;
    height: 50px;
    width: 50px;
    background-color: orangered;
    -webkit-box-shadow: 3px 3px var(--color-shadow);
            box-shadow: 3px 3px var(--color-shadow);
    color:white;
}
@-webkit-keyframes spin {
    25% {
      -webkit-transform: rotate(22deg);
              transform: rotate(22deg);
    }
    70% {
      -webkit-transform: rotate(25deg);
              transform: rotate(25deg);
    }
    100% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }
@keyframes spin {
    25% {
      -webkit-transform: rotate(22deg);
              transform: rotate(22deg);
    }
    70% {
      -webkit-transform: rotate(25deg);
              transform: rotate(25deg);
    }
    100% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }
  .sortButtonDiv:hover{
      cursor: pointer;
  }
  .sortButtonDiv i:hover{
      -webkit-animation:spin 1s;
              animation:spin 1s;
  }
  .sortButtonDiv:active {
    background-color: lightblue;
  }


